@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
  width: 100%;
  display: flex;
  /* justify-content: center; */
}

.center-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.App {
  background-color: #ffffff;
  /* padding: 20px; */
}

.error {
  color: #db2955;
  font-weight: bold;
}

/* User API Form */
.user-api-form .user-id,
.user-api-form .api-key {
  width: 300px;
}

.user-api-form .submit-wrapper {
  margin-top: 16px;
}

.user-api-form .label-container {
  display: flex;
  flex-direction: row;
}

.user-api-form .label {
  width: 80px;
}

.app-controls {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-controls .date-header {
  display: flex;
  flex-direction: column;
  /* margin-left: 12px; */
  font-size: 1.3em;
  color: #313131;
}

.identity {
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-top: 15px; */

  font-size: 1.3em;
  color: #313131;
}

/* Summary Section */
section {
  /* margin-top: 15px; */
}

.section-header,
.section-header-title {
  display: flex;
  align-items: center;
}

.show-no-history {
  margin-left: 8px;
}

section h2 {
  margin-top: 8px;
  margin-bottom: 8px;
}

td {
  height: 100%;
  padding: 0;
}

th {
  text-align: left;
}

.dailys table,
.habits table,
.todos table {
  width: 100%;
  border-collapse: separate;
}

.date {
  color: #757575;
}

.date-heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  font-weight: normal;
  font-size: medium;
}

.link {
  color: #3366bb;
}

.link:hover,
.clickable:hover {
  cursor: pointer;
}

/* Task */
.task-name-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.task-name {
  padding: 8px 12px 8px 0;
  max-width: 400px;
}

.task-name p {
  font-size: 14px;
  margin: 0;
}

.habit-cell,
.daily-cell {
  width: 40px;
}

.habit-score-container {
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: space-evenly;
  height: 40px;
}

/* Success/Fail Symbols */
.success {
  background-color: #24cc8f;
  flex: 1 1;
}

.fail {
  background-color: #db2955;
  flex: 1 1;
}

.pass,
.habit-cell {
  background-color: #ffdfae;
}

.pass .symbol {
  color: #ffbe5d;
}

.symbol {
  color: white;
}

.buttons-container {
  margin-top: 15px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.button-container {
  /* flex: 1; */
  /* justify-content: center;
  align-items: center;
  text-align: center; */
}

.dialog {
  width: -webkit-fit-content !important;
  width: fit-content !important;
  margin-top: 10vh !important;
  margin-left: 10vw !important;
}
